
* {
  box-sizing: border-box;
}

button {
  background-color: #48484e;
  padding: 6px 8px;
  color: #d9dfdf;
  border-radius: 4px;
  border: none;
  outline: none;
  opacity: .7;
  cursor: pointer;
}

button:hover {
  opacity: 1;
}

/* Control */
.control {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 1;
  height: 100px;
  opacity: 0;
  transition: opacity .3s;
}

.control .iconfont {
  font-size: 40px;
  color: #ffffff;
  margin: 16px;
  display: inline-block;
  cursor: pointer;
}

.control:hover {
  opacity: 1;
}


/* Menu */
.menu {
  position: fixed;
  left: 0;
  z-index: 100;
  width: 180px;
  height: 100vh;
  background: #252f2d;
  color: #747c7c;
  font-size: 13px;
  transform: translateX(-100%);
  transition: transform .3s;
}

.menu.open {
  transform: translateX(0);
}

.menu .icon-close {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  transition: color .3s;
}

.menu .icon-close:hover {
  color: #fff;
}

.menu .detail {
  margin-top: 50px;
}

.menu .detail .tab {
  display: flex;
  align-items: center;
  padding: 7px 20px;
  cursor: pointer;
}

.menu .detail .tab.active {
  color: #fff;
}

.menu .detail .iconfont {
  margin-right: 2px;
}

.menu .content {
  position: absolute;
  z-index: 999;
  left: 180px;
  top: 0;
  width: calc(100vw - 180px);
  height: 100vh;
  background: #212a29;
  font-size: 14px;
  transform: translateX(calc(100% + 180px)); 
  transition: transform .3s;
}

.menu.open .content.active {
  transform: translateX(0)
}

.menu .content .header{
  font-size: 40px;
  padding: 20px;
  border-bottom: 1px solid #425351;
}

.menu .content .body {
  padding: 20px;
}

.menu .content textarea {
  width: 100%;
  height: 400px;
  padding: 10px;
  background: #283231;
  color: #aaa;
  border: none;
  outline: none;
}

.menu .content button {
  margin-top: 16px;
}


